import CustomToggle from "components/shared/CustomToggle";
import { docmentsFromsKey } from "constant/data";
import React from "react";
import { Col, Row } from "react-bootstrap";
import DataTable from "react-data-table-component";
// needed styles
import pageStyles from "./index.module.scss";

const OrderTable = ({ setactive }) => {
  const columns = [
    {
      name: "المنتج",
      selector: (row) => row.product,
      sortable: true,
      grow: 4,
    },
    {
      name: "نوع التصديق",
      selector: (row) => row.type,
      sortable: true,
      grow: 2,
    },
    {
      name: "البلد ",
      selector: (row) => row.country,
      sortable: true,
      grow: 1,
    },
    {
      name: "السعر ",
      selector: (row) => row.price,
      cell: (row) => `${row?.price} ج.م`,
      sortable: true,
      grow: 1,
    },
    {
      grow: 1,
      cell: (row) => {
        return (
          <button
            onClick={() => console.log("delete")}
            className={pageStyles.deleteBtn}
          >
            <svg
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M4.227 4.227a.774.774 0 0 1 1.095 0L12 10.905l6.678-6.678a.774.774 0 1 1 1.095 1.095L13.095 12l6.678 6.678a.774.774 0 1 1-1.095 1.095L12 13.095l-6.678 6.678a.774.774 0 1 1-1.095-1.095L10.905 12 4.227 5.322a.774.774 0 0 1 0-1.095Z"
                fill="#0000007f"
              />
            </svg>
          </button>
        );
      },
    },
  ];

  //   table styles
  const customTableStyles = {
    rows: {
      style: {
        paddingTop: "40px",
        paddingBottom: "40px",
        borderBottom: "solid rgba(0,0,0,.12) 1px",
      },
    },
    headCells: {
      style: {
        fontSize: "22px",
        lineHeight: "29px",
        color: "#0000007f",
        fontWeight: "500",
      },
    },
    cells: {
      style: {
        fontSize: "22px",
        lineHeight: "29px",
        color: "#000000",
        fontWeight: "500",
      },
    },
  };
  const data = [
    {
      id: 1,
      product: "توثيق شهادة ميلاد",
      country: "السعودية",
      type: "ملحق ثقافي",
      price: 100,
    },
    {
      id: 1,
      product: "توثيق شهادة ميلاد",
      country: "السعودية",
      type: "ملحق ثقافي",
      price: 100,
    },
  ];

  const total = data.reduce((prev, curr) => prev?.price + curr?.price);
  return (
    <Row className={pageStyles.tableBody}>
      <Col xs={12}>
        <h2>توثيق الأوراق</h2>
      </Col>
      <Col xs={12} className={pageStyles.tableContainer}>
        <div className={pageStyles.tableContainerHeader}>
          <svg
            width="24"
            height="24"
            viewBox="0 0 24 24"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M3.75 5.25H6l.346 3m0 0 1.154 7.5h9.75l1.5-7.5H6.346Z"
              stroke="#FFAA03"
              strokeWidth="1.5"
              strokeLinecap="round"
              stroke-linejoin="round"
            />
            <path
              d="M9 18.75h.008m5.992 0h.008"
              stroke="#FFAA03"
              strokeWidth="2.25"
              strokeLinecap="round"
              stroke-linejoin="round"
            />
          </svg>
          <h3>تفاصيل سلة المشتريات</h3>
        </div>
        <DataTable
          columns={columns}
          data={data}
          customStyles={customTableStyles}
        />
        <Row className={pageStyles.moreInfo}>
          <Col xs={12} lg={4}>
            <label htmlFor="coupon">
              <svg
                width="24"
                height="24"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M21 5H3a1 1 0 0 0-1 1v4h.893c.996 0 1.92.681 2.08 1.664A2.001 2.001 0 0 1 3 14H2v4a1 1 0 0 0 1 1h18a1 1 0 0 0 1-1v-4h-1a2.001 2.001 0 0 1-1.973-2.336c.16-.983 1.084-1.664 2.08-1.664H22V6a1 1 0 0 0-1-1zM9 9a1 1 0 1 1 0 2 1 1 0 1 1 0-2zm-.8 6.4 6-8 1.6 1.2-6 8-1.6-1.2zM15 15a1 1 0 1 1 0-2 1 1 0 1 1 0 2z"
                  stroke="#0000007f"
                  fill="transparent"
                />
              </svg>
              <input
                type={"text"}
                placeholder="كود خصم"
                id="coupon"
                name="coupon"
              />
            </label>
          </Col>
          <Col xs={12} lg={2} />
          <Col xs={12} lg={3} className={pageStyles.total}>
            <p>
              <span>المجموع:</span>
              {`${total?.price ?? total} ج.م`}
            </p>
          </Col>
          {/* <Col xs={12} lg={3}>
            <CustomToggle
              eventKey={docmentsFromsKey.order}
              setter={() => setactive(docmentsFromsKey.order)}
            >
              أضف طلبات أخرى
            </CustomToggle>
          </Col> */}
        </Row>
      </Col>
    </Row>
  );
};

export default OrderTable;
