import React, { useEffect, useState } from "react";
import { docmentsFromsKey } from "constant/data";
import { Col, Row } from "react-bootstrap";
import { useFormContext } from "react-hook-form";
import { Link } from "react-router-dom";
// needed styles
import pageStyles from "./index.module.scss";

// needed components
import CustomToggle from "components/shared/CustomToggle";
import { FormattedMessage, useIntl } from "react-intl";
import { useSelector } from "react-redux";

const AddToCartPage = () => {
  const { register } = useFormContext();
  const [selectedDocType, setSelectedDocType] = useState(null);
  const [currentDocType, setCurrentDocType] = useState(null);
  const { formatMessage } = useIntl();
  const {
    meta: { locale },
    empolyee: { nationalties },
    document: { documentTypes },
  } = useSelector((state) => state);

  useEffect(() => {
    if (selectedDocType == null) return;
    setCurrentDocType(
      documentTypes.filter((type) => type._id === selectedDocType)[0]
    );
  }, [selectedDocType]);

  return (
    <Row className={pageStyles.orderContainer}>
      <Col xs={12} className={pageStyles.headers}>
        <h3>توثيق الأرواق</h3>
        <p>نقوم بتوثيق أوراقك الرسمية في الدولة التي تحتاج التوثيق لها</p>
      </Col>

      <Col xs={12} lg={10} className={pageStyles.form}>
        <Row className={pageStyles.customerInfoBody}>
          <Col className={pageStyles.customerInfoContainer}>
            <div className={`${pageStyles.formInput}  `}>
              <label htmlFor="name"> الاسم بالكامل </label>
              <input name="name" id="name" {...register("name")} />
            </div>
            <div className={`${pageStyles.formInput}  `}>
              <label htmlFor="phone">رقم التليفون</label>
              <input name="phone" id="phone" {...register("phone")} />
            </div>
            <div className={`${pageStyles.formInput}  `}>
              <label htmlFor="email">الإيميل</label>
              <input
                type={"email"}
                name="email"
                id="email"
                {...register("email")}
              />
            </div>
            <div className={`${pageStyles.formInput}  `}>
              <label htmlFor="address">العنوان بالتفصيل</label>
              <input name="address" id="address" {...register("address")} />
            </div>
          </Col>
        </Row>
        <div className={`${pageStyles.selectInput}  `}>
          <label htmlFor="country">
            <FormattedMessage id="Country" />
          </label>
          <select
            defaultValue={false}
            name="country"
            id="country"
            {...register("country", { required: true })}
          >
            <option value={false}>
              {formatMessage({ id: "selectCountry" })}
            </option>
            {nationalties.map((nationalty) => (
              <option value={nationalty._id}>
                {locale ? nationalty?.name_ar : nationalty?.name_en}
              </option>
            ))}
          </select>
        </div>
        <div className={`${pageStyles.selectInput}  `}>
          <label htmlFor="doc_type">
            <FormattedMessage id="documentType" />
          </label>
          <select
            defaultValue={false}
            name="doc_type"
            id="doc_type"
            {...register("doc_type", {
              required: true,
              onChange: (e) => setSelectedDocType(e.target.value),
            })}
          >
            <option value={false}>
              {formatMessage({ id: "selectDocumentType" })}
            </option>
            {documentTypes.map((docType) => (
              <option value={docType?._id}>
                {locale ? docType?.name_ar : docType?.name_en}
              </option>
            ))}
          </select>
        </div>
        {currentDocType?.cultural_supplement_price && (
          <div className={pageStyles.addFieldWithLabel}>
            <p>
              <label htmlFor="cultural_supplement">
                <FormattedMessage id="cultural_supplement" />
              </label>
            </p>
            <input
              type="checkbox"
              id="cultural_supplement"
              {...register("cultural_supplement")}
            />
            <span />
          </div>
        )}
        {currentDocType?.cultural_supplement_price && (
          <div className={pageStyles.addFieldWithLabel}>
            <p>
              <label htmlFor="embassy">
                <FormattedMessage id="embassy" />
              </label>
            </p>
            <input type="checkbox" id="embassy" {...register("embassy")} />
            <span />
          </div>
        )}
        {currentDocType?.cultural_supplement_price && (
          <div className={pageStyles.addFieldWithLabel}>
            <p>
              <label htmlFor="egyptian_foreign_ministry">
                <FormattedMessage id="egyptian_foreign_ministry" />
              </label>
            </p>
            <input
              type="checkbox"
              id="egyptian_foreign_ministry"
              {...register("egyptian_foreign_ministry")}
            />
            <span />
          </div>
        )}
        <div className={pageStyles.checkBox}>
          <label htmlFor="acceptPolicy">
            <input
              type="checkBox"
              id="acceptPolicy"
              {...register("acceptPolicy")}
            />
            <span>
              أوفق على <Link to="/"> الشروط والأحكام</Link>
            </span>
          </label>
        </div>
        <div className={pageStyles.submiteBtn}>
          <button type="submit">إرسال البيانات</button>
          {/* <CustomToggle
            eventKey={docmentsFromsKey.customer}
            setter={() => setactive(docmentsFromsKey.customer)}
          > */}
          {/* </CustomToggle> */}
        </div>
      </Col>
    </Row>
  );
};

export default AddToCartPage;
