import React, { useRef } from "react";
import { useAccordionButton } from "react-bootstrap";

const CustomToggle = ({
  children,
  eventKey,
  defaultClass,
  openClass,
  alwaysOpen,
  setter,
  activeKey,
  ...props
}) => {
  const toggleRef = useRef(null);
  const decoratedOnClick = useAccordionButton(eventKey, () => {
    setter && setter();
    toggleRef.current.classList.toggle(openClass);
  });

  return (
    <button
      type="button"
      onClick={decoratedOnClick}
      ref={toggleRef}
      className={`${defaultClass} ${alwaysOpen ? openClass : ""}`}
      disabled={activeKey === eventKey}
      {...props}
    >
      {children}
    </button>
  );
};

export default CustomToggle;
