import { docmentsFromsKey } from "constant/data";
import React, { useState } from "react";
import { Accordion, Col, Container, Row } from "react-bootstrap";
import { FormProvider, useForm } from "react-hook-form";
// needed styles
import pageStyles from "./index.module.scss";

// needed componets
import AddToCartPage from "./AddToCartPage";
import CustomerInfoForm from "./CustomerInfoForm";
import CardInfoForm from "./CardInfoForm";
import OrderTable from "./OrderTable";
import { toast } from "react-toastify";
import { useIntl } from "react-intl";
import { addDocument } from "api/documents";

const DocumentRequestPage = () => {
  const methods = useForm();
  const [active, setactive] = useState(docmentsFromsKey.order);
  const { formatMessage } = useIntl();
  //  handler Methods
  const submiteHandler = async (data) => {
    if (data.country === "false" || data.doc_type === "false") {
      toast.error(formatMessage({ id: "completeTheFom" }));
      return;
    }
    const response = await addDocument(data);
    if (response.startsWith("https://")) {
      // navigate(response, { state: 301, replace: true });
      window.location.href = response;
    }
  };
  return (
    <FormProvider {...methods}>
      <form
        onSubmit={methods.handleSubmit(submiteHandler)}
        className={pageStyles.body}
      >
        <Container>
          <Row>
            <Col xs={12}>
              <Accordion defaultActiveKey={active} flush={true}>
                <Col
                  xs={12}
                  className={`${
                    active !== docmentsFromsKey.order
                      ? pageStyles.showTable
                      : pageStyles.hideTable
                  } `}
                >
                  <OrderTable
                    {...{
                      setactive: setactive,
                    }}
                  />
                </Col>
                <Accordion.Item
                  className={`${
                    active === docmentsFromsKey.order
                      ? pageStyles.accordionItem
                      : ""
                  } ${pageStyles.accordionDefualtItem}`}
                  eventKey={docmentsFromsKey.order}
                >
                  <Accordion.Body>
                    <AddToCartPage
                      {...{
                        setactive: setactive,
                      }}
                    />
                  </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item
                  className={`${
                    active === docmentsFromsKey.customer
                      ? pageStyles.accordionItem
                      : ""
                  } ${pageStyles.accordionDefualtItem}`}
                  eventKey={docmentsFromsKey.customer}
                >
                  <Accordion.Body>
                    <CustomerInfoForm
                      {...{
                        setactive: setactive,
                      }}
                    />
                  </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item
                  className={`${
                    active === docmentsFromsKey.card
                      ? pageStyles.accordionItem
                      : ""
                  } ${pageStyles.accordionDefualtItem}`}
                  eventKey={docmentsFromsKey.card}
                >
                  <Accordion.Body>
                    <CardInfoForm />
                  </Accordion.Body>
                </Accordion.Item>
              </Accordion>
            </Col>
          </Row>
        </Container>
      </form>
    </FormProvider>
  );
};

export default DocumentRequestPage;
